<script>
    import RightSide from './public/RightSide';
    import HeaderBar from './public/HeaderBar';
    import FooterBar from './public/FooterBar';
    import FooterContent from './public/FooterContent';

    export default {
        components: {
            FooterBar,
            FooterContent,
            HeaderBar,
            RightSide,
        },
        data() {
            return {
                orderDetail: {
                    address: '',
                    money: 1034.88,
                    name: 'Purrfectdiary  1-811',
                    num: 3,
                    number: 1122154857863,
                    phone: 12345654321,
                    user: '',
                    size: 'M',
                },
            };
        },
    };
</script>
<template>
    <div class="order-success">
        <header-bar></header-bar>
        <div class="header-bar-logo header-bar-line">
            <div class="container">
                <img src="../../../user/src/assets/images/logo.png" alt="">
            </div>
        </div>
        <div class="padding-attribute container">
            <div class="submit-success" v-if="orderDetail">
                <div class="submit-title text-center">
                    <span>3000</span>~
                </div>
                <p>{{ orderDetail.number }}</p>
                <p>{{ orderDetail.address }}{{ orderDetail.user }}{{ orderDetail.phone }} </p>
                <p>{{ orderDetail.name }}
                    <span>{{ orderDetail.num }}</span> <span>: {{ orderDetail.size }}</span></p>
            </div>
            <div class="order-pay">
                <div class="need-pay">
                    : <span> {{ orderDetail.money }}</span>
                </div>
                <p class="alipay">
                    <label class="form-control-radio">
                        <input type="radio" name="pay">
                        <span></span>
                        <img src="../../../user/src/assets/images/alipay.png" alt="alipay">
                    </label>
                </p>
                <p class="weixin">
                    <label class="form-control-radio">
                        <input type="radio" name="pay">
                        <span></span>
                        <img src="../../../user/src/assets/images/weixin.png" alt="weixin">
                    </label>
                </p>
                <router-link :to="{ name: 'pay-result' }" class="order-btn"></router-link>
            </div>
        </div>
        <right-side></right-side>
        <footer-content></footer-content>
        <footer-bar></footer-bar>
    </div>
</template>



